import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import './index.css'
import Rating from '../rating'
import Detail from '../musicdetail'

class Item extends Component {
  render () {
    const item = this.props.item
    const id = item.id
    return (
      <li className='list-item'>
        <Link to={
          {
            pathname: '/' + id,
            state: { id: id }
          }
        } className='list-item' component={Detail}>
          <img src={item.image} alt={item.title} class='cover' />
          <div className='list-item-info'>
            <h2>{item.title}<span className='year'>（{item.attrs.pubdate[0]}）</span></h2>
            <div className='rating'>
              <Rating item={item.rating} />
              <span>{item.rating.average}</span>
            </div>
            <div className='tags'>
              {item.tags.map((string) => {
                return (
                  <span>{string.name}</span>
                )
              })}
            </div>
            <div className='series'>
              <span>{item.author.map((string) => {
                return (
                  <span>{string.name}</span>
                )
              })}</span>
            </div>
          </div>
        </Link>
      </li>
    )
  }
}

export default Item
