import React, { Component } from 'react'

import './style.css'

import Main from './main'

class App extends Component {
  constructor () {
    super()
    this.state = {
      keyword: ''
    }
  }
  onChange (value) {
    this.setState({
      keyword: value
    })
  }
  render () {
    return (
      <div className='app'>
        <Main />
      </div>
    )
  }
}

export default App
