import React, { Component } from 'react'
import './index.css'
import fetchJsonp from 'fetch-jsonp'
import Search from '../search'
import Item from '../movieitem'
import Tab from '../tab'

class List extends Component {
  constructor (props) {
    super(props)
    this.state = {
      api: 'https://api.douban.com/v2/movie/top250',
      apiArr: [],
      apiItem: 'movies',
      placeholder: '电影、影人、影院、电视剧'
    }
  }
  // 渲染前首次拉取接口
  componentWillMount () {
    const self = this
    fetchJsonp(this.state.api)
      .then(response => {
        return response.json()
      }).then(data => {
        console.log('parsed data', data)
        self.setState({
          apiArr: data.subjects
        })
      }).catch(ex => {
        console.log('parsing failed', ex)
      })
  }
  onChange (value) {
    console.log('onChange (value)', value)
    this.setState({
      api: 'https://api.douban.com/v2/movie/search?q=' + value
    })
  }
  render () {
    const apiArr = this.state.apiArr
    let itemState = this.state
    return (
      <div>
        <Search item={itemState} onChange={this.onChange.bind(this)} />
        <div className='list'>
          <ul>
            {apiArr.map((item, index) => {
              return (
                <Item item={item} key={index} index={index} />
              )
            })}
          </ul>
        </div>
        <Tab />
      </div>
    )
  }
  componentDidUpdate (prevProps, prevState) {
    // api 有更新则拉取接口
    if (this.state.api !== prevState.api) {
      console.log('Component DID UPDATE!')
      const self = this
      fetchJsonp(this.state.api)
        .then(response => {
          return response.json()
        }).then(data => {
          console.log('parsed data', data)
          self.setState({
            apiArr: data.subjects
          })
        }).catch(ex => {
          console.log('parsing failed', ex)
        })
    }
  }
}

export default List
