import React, { Component } from 'react'
import './index.css'
import fetchJsonp from 'fetch-jsonp'
import Rating from '../rating'

class Detail extends Component {
  constructor (props) {
    super(props)
    this.state = {
      data: {
        author: [],
        rating: [],
        images: [],
        tags: []
      },
      id: this.props.location.state.id
    }
    console.log('id', this.state.id)
  }
  // 渲染前首次拉取接口
  componentWillMount () {
    const self = this
    fetchJsonp('https://api.douban.com/v2/book/' + this.state.id)
      .then(response => {
        return response.json()
      }).then(data => {
        console.log('parsed data', data)
        self.setState({
          data: data
        })
      }).catch(ex => {
        console.log('parsing failed', ex)
      })
  }
  render () {
    const data = this.state.data
    return (
      <div>
        <div className='back'>
          <a className='back-btn'>图书</a>
          <span>{data.title}</span>
        </div>
        <div className='intro'>
          <div className='left'>
            <h1>{data.title}</h1>
            <p className='rating-text'>
              <Rating item={data.rating} />{data.rating.average}
              <span className='num-raters'>{data.rating.numRaters}人评价</span>
            </p>
            <p className='meta'>
              {data.author.map((string) => {
                return (
                  <span>{string}</span>
                )
              })}
              {data.publisher} / {data.pubdate} / {data.binding} / {data.pages}页 / {data.price}</p>
            <p className='tag-name'>
              {data.tags.map((string) => {
                return (
                  <span>{string.name}</span>
                )
              })}
            </p>
            {/* <p>导演: 闫非 / 彭大魔</p>
            <p>主演: 沈腾 / 宋芸桦 / 张一鸣 / 张晨光 / 常远 / </p>
            <p>类型: 喜剧</p>
            <p>制片国家/地区: 中国大陆</p>
            <p>语言: 汉语普通话</p>
            <p>上映日期: 2018-07-27(中国大陆)</p>
            <p>片长: 118分钟</p> */}
          </div>
          <img src={data.images.small} className='cover' />
        </div>
        <div className='info'>
          <h2>简介</h2>
          <p>{data.summary}</p>
        </div>
        <div className='info'>
          <h2>作者</h2>
          <p>{data.author_intro}</p>
        </div>
      </div>
    )
  }
}

export default Detail
